import axios from 'axios'
import { useState } from 'react'
import Header from '../components/Header'
import Hero from '../components/Hero'
import './Home.scss'

const Homepage = () => {

    const [location, setLocation] = useState({
        city: 'tashkent',
        country: 'Uzbekistan'
    })

    const [locationData, setLocationData] = useState({
        isFetched: false,
        data: {},
        error: null
    })

    const search = () => {
        axios
          .get(
            `http://api.aladhan.com/v1/timingsByCity?city=${location.city}&country=uzbekistan&method=8`
          )
          .then(function (response) {
            setLocationData({
              isFetched: true,
              data: response.data,
              error: null,
            });
          })
          .catch(function (error) {
            setLocationData({
              isFetched: true,
              data: {},
              error: error,
            });
          });
        }

        return (
          <div>
            <Header />
            <Hero
              location={location}
              search={search}
              setLocation={setLocation}
              locationData={locationData}
            />
          </div>
        );
    }
export default Homepage    
