import { useState } from 'react';
import styled from 'styled-components';
import './Hero.scss'
const Hero = ({ locationData, setLocation, location, search }) => {
const [time, setTime] = useState('-- : -- : --')
  const timeHandler = () => {
    let currentTime = new Date().toLocaleTimeString();
    setTime(currentTime)
  }
  
  setInterval(() => {
    timeHandler()
  }, 1000);
  
  return (
    <div>
      <div>
        <div className="logo">Joylashuvingizni tanlang:</div>
      

        <select
          name="country"
          id="country"
          className="country-selector"
            onChange = {
              (e) =>
              setLocation({
                city: e.target.value,
                country: location.country,
              })
            }
        >
          <option value="tashkent">Tashkent</option>
          <option value="kokand">Kokand</option>
          <option value="samarkand">Samarkand</option>
          <option value="bukhara">Buxoro</option>
          <option value="fergana">Farg'ona</option>
          <option value="namangan">Namangan</option>
          <option value="andijan">Andijon</option>
          <option value="gulistan">Guliston</option>
          <option value="khorezm">Xorazm</option>
          <option value="termez">Termiz</option>
        </select>

        <button onClick={search} className="search-button">Izlash</button>
      </div>
        <CurrentTime>hozir-{time} </CurrentTime>

      {locationData.isFetched ? (
        <div className="content">
          <h2>
            <img
              width="30px"
              src="https://www.flaticon.com/premium-icon/icons/svg/2320/2320034.svg"
              alt=""
            />
            Tong: {locationData.data.data.timings["Fajr"]}
          </h2>
          <h2>
            <img
              width="30px"
              src="https://www.flaticon.com/premium-icon/icons/svg/2320/2320041.svg"
              alt=""
            />
            Peshin: {locationData.data.data.timings["Dhuhr"]}
          </h2>
          <h2>
            <img
              width="30px"
              src="https://www.flaticon.com/premium-icon/icons/svg/2320/2320047.svg"
              alt=""
            />
            Asr: {locationData.data.data.timings["Asr"]}
          </h2>
          <h2>
            <img
              width="30px"
              src="https://www.flaticon.com/premium-icon/icons/svg/2320/2320053.svg"
              alt=""
            />
            Shom: {locationData.data.data.timings["Maghrib"]}
          </h2>
          <h2>
            <img
              width="30px"
              src="https://www.flaticon.com/premium-icon/icons/svg/2320/2320056.svg"
              alt=""
            />
            Hufton: {locationData.data.data.timings["Isha"]}
          </h2>
        </div>
      ) : (
        <p>{''}</p>
      )}
    </div>
  );
};
const CurrentTime = styled.p`
  color: #fff;
  margin: 0;
  font-weight: bold;
  font-family: monospace;
  font-size: 18px
`
export default Hero
